<template>
  <section>
    <div class="container py-5">
      <div class="row">
        <h4 class="text-left mb-3">
          美食饗宴
        </h4>
      </div>
      <RegionSelect @selectCity="selectCityHandler" />
    </div>
    <div class="container pb-5">
      <div class="row mb-4">
        <div
          v-if="restaurantsByCity?.length"
          class="d-flex align-items-center h5"
        >
          <div class="me-3">
            {{ currentCity.CityName }}
          </div>
          <small class="text-small text-secondary">共有 <span class="text-success">{{ allDataLength }}</span> 個特色活動</small>
        </div>
        <div
          v-else
          class="me-3 text-small text-secondary"
        >
          暫無相關資料。
        </div>
      </div>
      <div class="row">
        <div class="col-xl-9">
          <div
            v-for="restaurant in restaurantsByCity"
            :key="restaurant.RestaurantID"
            class="mb-3"
          >
            <RestaurantCard
              :item="restaurant"
              :title="restaurant.RestaurantName"
              :type="restaurant.Class"
              :content="restaurant.Description"
              :location="restaurant.City"
              @clickHandler="clickHandler"
            />
          </div>
        </div>
        <div class="col-xl-2 bg-light-secondary p-5 ms-auto align-self-start">
          <h5 class="mb-4">
            分類篩選
          </h5>
          <div class="d-flex flex-column">
            <CheckBox class="mb-2" />
            <CheckBox
              class="mb-2"
              title="中式美食"
              :disabled="true"
              :checked="false"
            />
            <CheckBox
              class="mb-2"
              title="異國料理"
              :disabled="true"
              :checked="false"
            />
            <CheckBox
              class="mb-2"
              title="地方名產"
              :disabled="true"
              :checked="false"
            />
          </div>
        </div>
      </div>
      <div class="row my-5">
        <div
          v-if="restaurantsByCity?.length"
          class="d-flex justify-content-center"
        >
          <Pagination
            :current-page="page"
            :pages="pages"
            @changePage="changePageHandler"
          />
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { useStore } from 'vuex'
import { ref, watch, computed } from 'vue'
import RestaurantCard from '@/components/RestaurantCard'
import Pagination from '@/components/Pagination'
import CheckBox from '@/components/CheckBox'
import RegionSelect from '@/components/RegionSelect'
export default {
  name: 'Cities',
  components: {
    RestaurantCard,
    CheckBox,
    Pagination,
    RegionSelect
  },
  setup () {
    const store = useStore()
    const currentCity = ref({})
    const page = ref(1)
    const pages = computed(() => store.getters.pages)
    const restaurantsByCity = computed(() => store.getters.restaurantsByCity)
    const allDataLength = computed(() => store.getters.allDataLength)
    const selectCityHandler = (city) => {
      currentCity.value = city
    }

    watch(currentCity, (newValue) => {
      if (newValue) {
        getRestaurantHandler(newValue.CityEngName)
      }
    })

    const clickHandler = (content) => {
      window.open('https://www.google.com.tw/search?q=' + content.RestaurantName, '_blank')
    }

    const getRestaurantHandler = (city) => {
      store.dispatch('GET_RESTAURANT_BY_CITY', {
        city: city,
        sorted: {
          $top: 30,
          $format: 'JSON'
        }
      })
      store.dispatch('GET_RESTAURANT_BY_CITY_All_PAGES', city)
      resetPage()
    }

    const resetPage = () => {
      page.value = 1
    }

    const scrollToTop = () => {
      window.scrollTo(0, 0)
    }

    const changePageHandler = (index) => {
      page.value = index
      store.dispatch('GET_RESTAURANT_BY_CITY', {
        city: currentCity.value.CityEngName,
        sorted: {
          $top: 30,
          $format: 'JSON',
          $skip: (page.value - 1) * 30
        }
      })
      scrollToTop()
    }

    return {
      page,
      pages,
      selectCityHandler,
      currentCity,
      changePageHandler,
      restaurantsByCity,
      clickHandler,
      allDataLength
    }
  }
}
</script>

<style lang="scss" scoped>
.badge{
  position: relative;
  cursor: pointer;
  &.active {
  &::before {
    content: "";
    position: absolute;
    display: block;
    left: 50%;
    bottom: .35rem;
    width: .85rem;
    height: 4px;
    border-radius: 6px;
    background-color:  #10CC9F;
    transform:translate(-50%,0);
  }
  }
}

.icon{
  width: 1.45rem;
  transform: translateX(-.75rem);
}

.bg-dark-light{
  background:  #F2F2F2;
  border-right:1px solid #DEDEDE;
  position: relative;
   &::before {
    position: absolute;
    width: 1rem;
    height: 1rem;
    right: -1rem;
    top: 50%;
    transform: translateY(-50%);
    content: "";
    background-image: url("../assets/image/Polygon.svg");
    background-repeat: no-repeat;
    background-size: contain;
  }
}

.border-left-radius{
  border-top-left-radius: 0.75rem;
  border-bottom-left-radius: 0.75rem;
}

.border-right-radius{
  border-top-right-radius: 0.75rem;
  border-bottom-right-radius: 0.75rem;
}

.bg-light-secondary{
  border-radius: 10px;
  background: #F6F6F6;;
}
</style>
