<template>
  <div class="card mb-4">
    <div class="row no-gutters">
      <div class="col-md-3">
        <img
          :src="renderImage(item?.Picture?.PictureUrl1)"
          class="w-100 object-cover cursor-pointer"
          :alt="item?.Picture?.PictureDescription1||''"
          style="max-height:185px"
          @click="clickHandler(item)"
        >
      </div>
      <div class="col-md-9">
        <div class="card-body d-flex flex-column h-100">
          <h5 class="card-title mb-2">
            {{ title }}
          </h5>
          <small class="d-block text-primary mb-2">
            {{ type }}
          </small>
          <small class="card-text text-secondary">
            {{ content.length > 120 ? content.substring(0,120)+'...' : content }}
          </small>
          <p class="card-text mt-auto">
            <small class="text-muted">{{ location }}</small>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      default: () => {}
    },
    title: {
      type: String,
      default: ''
    },
    content: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: ''
    },
    location: {
      type: String,
      default: ''
    }
  },
  setup (props, { emit }) {
    const initImage = 'https://images.unsplash.com/photo-1543161252-42609aa0e3d2?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1017&q=80'

    const clickHandler = (content) => {
      emit('clickHandler', content)
    }

    const renderImage = (image) => {
      return image || initImage
    }

    return {
      initImage,
      clickHandler,
      renderImage
    }
  }
}
</script>

<style scoped>
.card{
  border:none;
  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
  border-radius: 10px !important;
  overflow: hidden;
}

</style>
